import React from 'react';
import { useNavigate } from 'react-router-dom';
import {
  ChatWrapper,
  List,
  ProfileImg,
  ProfileDot,
  TextWrap,
  UserName,
  ChatContent,
  Date,
} from './ChatListItemStyle';
import userProfile from '../../images/basic-profile-img.svg';

export default function ChatListItem() {
  const chatListData = [
    {
      id: 1,
      profileImg: userProfile,
      username: '',
      chatContent: '   !',
      date: '2023.10.28',
    },
    {
      id: 2,
      profileImg: userProfile,
      username: '',
      chatContent: '   ',
      date: '2023.10.30',
    },
    {
      id: 3,
      profileImg: userProfile,
      username: '',
      chatContent: '  !',
      date: '2023.11.3',
    },
  ];
  const navigate = useNavigate();
  function handleClick() {
    navigate('/chatroom/example');
  }
  return (
    <ChatWrapper>
      {chatListData.map((chatItem) => (
        <List key={chatItem.id} onClick={handleClick}>
          <ProfileImg src={chatItem.profileImg} alt=' ' />
          <ProfileDot />
          <TextWrap>
            <UserName>{chatItem.username}</UserName>
            <ChatContent>{chatItem.chatContent}</ChatContent>
          </TextWrap>
          <Date>{chatItem.date}</Date>
        </List>
      ))}
    </ChatWrapper>
  );
}
