import React from 'react';
import LogoWhiteImg from '../../images/Logo_white.svg';
import { Container, Logo, Login, Join } from './WelcomeStyle';

export default function Welcome() {
  return (
    <Container>
      <Logo src={LogoWhiteImg} alt='' />
      <Login to='/login'> </Login>
      <Join to='/signup'> </Join>
    </Container>
  );
}
