import React from 'react';
import {
  LoginWrap,
  LoginTitle,
  SignUpLink,
  SimpleLoginWrap,
  SnsButton,
  SnsList,
} from './StyledLogin';
import LoginForm from '../../components/Login/LoginForm';
import sprite from '../../images/SpriteIcon.svg';

export default function Login() {
  const SnsSVG = ({ id, size = 24 }) => (
    <svg width={size} height={size}>
      <use href={`${sprite}#${id}`} />
    </svg>
  );

  return (
    <>
      <h1 className='a11y-hidden'> </h1>
      <LoginWrap>
        <LoginTitle> !</LoginTitle>
        <LoginForm />
        <SignUpLink to='/signup'>  ?</SignUpLink>
        <SimpleLoginWrap>
          <h2 className='a11y-hidden'> </h2>
          <SnsList>
            <li>
              <SnsButton $color='kakao'>
                <SnsSVG id='kakao' />
                <h3 className='a11y-hidden'>  </h3>
              </SnsButton>
            </li>
            <li>
              <SnsButton $color='google'>
                <SnsSVG id='google' />
                <h3 className='a11y-hidden'>  </h3>
              </SnsButton>
            </li>
            <li>
              <SnsButton $color='github'>
                <SnsSVG id='github' />
                <h3 className='a11y-hidden'>  </h3>
              </SnsButton>
            </li>
            <li>
              <SnsButton $color='facebook'>
                <SnsSVG id='facebook' />
                <h3 className='a11y-hidden'>  </h3>
              </SnsButton>
            </li>
          </SnsList>
        </SimpleLoginWrap>
      </LoginWrap>
    </>
  );
}
